@import url(https://fonts.googleapis.com/css2?family=Goldman:wght@700&family=Montserrat:wght@500&family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Goldman:wght@700&family=Modak&family=Montserrat:wght@500&family=Noto+Sans+KR:wght@900&family=Oswald&family=Slabo+27px&family=Stalinist+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Xanh+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
}
#hero {
  background-image: url(/ManchesterUnited/static/media/Manchester_United_wallpaper_with_logo_1920x1200.474ffa3a.jpg);
  background-size: cover;
  background-position: top center;
  position: relative;
  z-index: 1;
  /* filter: brightness(50%); */
}

#hero .container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 50px;
  justify-content: flex-start;
}

#hero::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.7;
  background-color: black;
  z-index: -1;
}
#information a {
  border: 2px solid red;
  background-color: transparent;
  text-decoration: none;
  padding: 5px 20px;
  letter-spacing: 0.2rem;
  margin-top: 20px;
  color: white;
  position: relative;
  top: 20px;
  font-family: "Goldman", cursive;
  transition: 0.5s ease background-color;
}
#information h1 {
  font-size: 2rem;
  margin-top: 10px;
  font-family: "Montserrat", sans-serif;
  position: relative;
  color: transparent;
  animation: text-color-appear 0.5s ease forwards;
}
#information a:hover {
  background-color: red;
}
#information h1:nth-child(1) {
  animation-delay: 1s;
}
#information h1:nth-child(2) {
  animation-delay: 2s;
}
#information h1:nth-child(3) {
  animation-delay: 3s;
}
#information h1:nth-child(1) span {
  animation-delay: 0.5s;
}
#information h1:nth-child(2) span {
  animation-delay: 1.5s;
}
#information h1:nth-child(3) span {
  animation-delay: 2.5s;
}
#information h1 span {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 0%;
  background-color: red;
  animation: text_reveal 1s ease;
}
@keyframes text_reveal {
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes text-color-appear {
  100% {
    color: white;
  }
}
h1 {
  display: inline-block;
}
#information {
  width: 300px;
}
@media all and (max-width: 650px) {
  #information h1 {
    font-size: 1.5rem;
  }
  #information {
    width: 250px;
  }
}

#player #container {
  display: flex;
  flex-direction: column;
  width:100%;
  height: auto;
  background-color: black;
}
#project-item {
  border-radius: 50px;
}
#player #container #header h1 {
  font-size: 120px;
  letter-spacing: 4px;
  font-family: 'Architects Daughter', cursive;
  display: block;
  text-align: center;
  color: white;
}
#player #container {
  padding-bottom: 50px;
}
#project-item:nth-child(even){
  flex-direction: row-reverse;
}
#project-item {
  display: flex;
  align-items: center;
  flex-direction: row;
  height: 300px;
  width: 50%;
  /* max-width: 1200px; */
  margin:0 auto;
  background-color: brown;
}
#all-projects{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
#project-item #player-name {
  flex-basis: 50%;
  padding: 40px;
  height: 100%;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-family: 'Slabo 27px', serif;
  letter-spacing: 1px;
  color: white;
  background-color: black;
}
#project-item #player-name h1 {
  font-size: 30px;
  font-family: 'Slabo 27px', serif;
}
#image {
  width: 50%;
  text-align: center;
}
#image img {
  width: 300px;
  transition: .5s ease transform;
  padding:25px;
  cursor: pointer;
  object-fit: cover;
}
#image img:hover {
  transform: scale(1.2);
}
@media all and (max-width: 1200px) {
  #project-item {
    width:70%;
  }
}
@media all and (max-width: 900px) {
  #project-item {
    width:100%;
  }
}
@media all and (max-width: 650px) {
  #project-item #player-name h2{
    font-size: 15px;
  }
  #project-item #player-name h1 {
    font-size: 20px;
  }
}
@media all and (max-width : 600px) {
  #project-item {
    flex-direction: column;
    height: auto;
    width: 80%;
    margin-bottom: 20px;
  }
  #project-item:nth-child(even){
    flex-direction: column;
  }
  #image {
    width: 100%;

  }
  #project-item #player-name {
    width: 100%;
    padding: 10px;
  }
  #image img {
    width: 200px;
  }
  #player #container #header h1 {
    font-size: 80px;
  }
}
@media all and (max-width : 400px) {
  #player #container #header h1 {
    font-size: 60px;
  }
}


.footer {
  background-color: black;
  width: 100%;
  height: 70px;
}

#containerjs {
  max-width: 1000px;
  height: 100%;
  margin: 0 auto;
  background-color: black;
  display: flex;
  align-items: center;
  border-left:5px solid red;
  border-right: 5px solid red;
  border-top: 1px solid white;
}
#containerjs h1 {
  color: white;
  width: 100%;
  text-align: center;
  letter-spacing: 5px;
  font-family: 'Xanh Mono', monospace;
}
#foo11 , 
#foo22 , 
#foo33 {
  width: 50px;
  height: 50px;
}
#foo2 {
  margin-left:auto;
}
#foo1 {
  margin-left: 20px;
  transition: .5s ease transform;
  cursor: pointer;
}
#foo3 ,
#foo2 {
  margin-right: 20px;
  transition: .5s ease transform;
  cursor: pointer;
}
#foo1:hover , 
#foo2:hover ,
#foo3:hover {
  transform: scale(1.1);
}
@media all and (max-width:700px) {
  #containerjs {
    flex-wrap: wrap;
    height: auto;
  }
  #containerjs h1{
    flex: 1 1 100%;
  }
  #foo1 {
    flex: 1 1 100%;
    text-align: center;
    margin-left: 0px;
    margin-top: 10px;
    margin-bottom: 10px;

  }
  #foo2 , 
  #foo3 {
    flex: 1 1 45%;
    text-align: center;
    margin-right: 0px;
    margin-bottom: 10px;
    margin-top: 10px;
  }
}


#headerHamburger {
  position: fixed;
  top:0 ; 
  left: 0;
  width: 100%;
  min-height: 80px;
  z-index: 1000;
  color: white;
  background-color: rgb(20, 1, 1 , 0.2);

  
}

#headerHamburger #navbar {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10px;
}
#headerHamburger #navbar #navlist ul li {
  list-style: none;
}
#headerHamburger #navbar #navlist ul li a{
  list-style: none;
  font-weight: 500;
  font-size: 1.5rem;
  letter-spacing: .3rem;
  padding: 20px;
  text-transform: uppercase;
  display: block;
  text-decoration: none;
  color: white;
  text-decoration: none;
  transition: .5s ease color;
}
#headerHamburger #navbar #navlist ul {
  position: absolute;
  top:0 ; 
  /* left: 0 ; */
  width: 100%;
  height: 100vh;
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 1;
  transition: .7s ease left;
}


#headerHamburger #navbar #navlist ul a::after{
   content: attr(data);
   position: absolute;
   top:50%;
   left: 50%;
   transform: translate(-50% , -50%) scale(0) ;
   font-size: 5rem;
   z-index: -1;
   letter-spacing: 10px;
   width: 100%;
   color: white;
   opacity: 0.1;
   text-align: center;
}

#headerHamburger #navbar #navlist ul li:hover a::after {
  transform: translate(-50% , -50%) scale(1) ;
}
#headerHamburger #navbar #navlist ul li:hover a{
  color: red;
}

#hamburger {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 60px;
  height: 60px;
  z-index: 1000;
  border-radius: 50%;
  border: 3px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  
}
#hamburger:after {
  content: "";
  position: absolute;
  border-radius: 50%;
  border: 3px solid white;
  width: 100%;
  height: 100%;
  animation: 1s ease border-reveal infinite;
}
#hambLine {
  width: 30px;
  height: 3px;
  background-color: cornsilk;
  position: relative;
}
#hambLine::after ,
#hambLine::before{
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: cornsilk;
  transition: .5s ease ;
  transition-property: top , bottom;
}
#team {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 20px;
  margin-top: 15px;
}
#hambLine::after {
  top: 8px;
}
#hambLine::before {
  bottom:8px;
}
#hambLine.blue::after {
  top: 8px;
}
#hambLine.blue::before {
  bottom:8px;
}
#hambLine.red::after {
  top: 0px;
}
#hambLine.red::before {
  bottom:0px;
}

@keyframes border-reveal {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.3);
  }
}

@media all and (min-width:1200px) {
  #hamburger {
    display: none;
  }
  #headerHamburger #navbar #navlist ul {
    position: static;
    position: initial;
    display: block;
    height: auto;
    background-color: transparent;
    
  }
  #headerHamburger #navbar #navlist ul li {
    display: inline-block;
  }
  #headerHamburger #navbar #navlist ul li a{
    font-size: 1rem;
  }
  #headerHamburger #navbar #navlist ul a::after {
    display: none;
  }

}
